.sitemap {
  display: flex;
  flex-direction: column;

  li {
    margin-bottom: 20px;
  }
}

.profile {
  display: flex;
  min-height: 100%;

  &__content {
    height: 100%;
    min-height: 100vh;
    width: 100%;

    display: flex;
    justify-content: center;
    padding: 60px;
  }

  &__wrapper {
    max-width: 510px;
    width: 100%;
  }

  &__back-btn {
    min-height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    background: #FBFBFB;
    max-width: 64px;
    width: 64px;

    border: 0;

    cursor: pointer;

    padding: 0;
    margin: 0;

    svg {
      transform: rotate(180deg);
    }

     span {
       width: 28px;
       height: 28px;
       background: #3369F3;
       border-radius: 50%;
       display: flex;
       align-items: center;
       justify-content: center;
     }
  }
}
